import React from 'react';
import { Button, Table, Modal, Form, Input } from 'semantic-ui-react';
import { db } from '../utils/firebase';
export default function AutoTable(props) {
  const defalutItem = props.defalutItem;
  const schema = props.schema;
  // const data = props.data;
  // 
  const [itemList, setItemList] = React.useState([]);
  // 
  const [item, setItem] = React.useState(defalutItem);
  // 
  const [editedIndex, setEditedIndex] = React.useState(-1);
  // Modal
  const [open, setOpen] = React.useState(false);

  const dbCol = db.collection(props.collectionName);

  React.useEffect(() => {
    dbCol.get().then((snapshot) => {
      const rows = snapshot.docs.map((doc) => {
        return { ...doc.data(), id: doc.id };
      });
      setItemList(rows);
    });
  }, []);

  // CRUD
  function handleEdit(obj) {
    setOpen(true);
    // ,,
    setEditedIndex(itemList.indexOf(obj));
    setItem(obj);
  }

  function handleUpdate() {
    // 
    if (editedIndex == -1) {
      const row = { ...item, id: Date.now() };
      dbCol.add(row);
      setItemList([...itemList, row]);
    }
    // 
    else {
      dbCol.doc(item.id).update(item);

      // 
      const data = itemList.slice();
      // 

      Object.assign(data[editedIndex], item);
      // 
      setItemList(data);
      setEditedIndex(-1);
    }

    // 
    setItem(defalutItem);
    setOpen(false);
  }

  function handleDelete() {
    if (!confirm('?')) return;

    dbCol.doc(item.id).delete();
    const data = itemList.filter((obj) => obj.id !== item.id);
    setItemList(data);
    setOpen(false);
  }

  
  return (
    <>
      {/* <pre>{JSON.stringify(item)}</pre> */}
      {/* <pre>{editedIndex}</pre> */}
      <Modal open={open} closeIcon onClose={() => setOpen(false)}>
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Form>
            {/*  */}
            {schema.map((obj, i) => {
              return (
                <Form.Field key={i}>
                  <label>{obj.text}</label>
                  <Input
                    placeholder={obj.text}
                    name={obj.name}
                    value={item[obj.name]}
                    type={obj.type}
                    onChange={(e) => {
                      setItem({ ...item, [e.target.name]: e.target.value });
                    }}
                  />
                </Form.Field>
              );
            })}
          </Form>
        </Modal.Content>
        <Modal.Actions>
          <button className="ui button blue" onClick={handleUpdate}>
            
          </button>
          {editedIndex !== -1 && (
            <button
              className="ui button red left floated"
              onClick={handleDelete}
            >
              
            </button>
          )}
        </Modal.Actions>
      </Modal>

      <button
        className="ui button"
        onClick={() => {
          setOpen(true);
          setItem(defalutItem);
          setEditedIndex(-1);
        }}
      >
        
      </button>

      <MyTable edit={handleEdit} schema={schema} rows={itemList} dataRow={props.dataRow}  />
    </>
  );
}

class MyTable extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <>
        <Table unstackable>
          <Table.Header>
            <Table.Row>
              {/*  */}
              {this.props.schema.map((header, i) => {
                return (
                  <Table.HeaderCell key={i}>{header.text}</Table.HeaderCell>
                );
              })}
              <Table.HeaderCell>#</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {/*  */}
            {this.props.rows.map((row, i) => {
              return (
                <tr key={i}>
                  {/* () */}
                  {this.props.dataRow
                    ? this.props.dataRow
                    : this.props.schema.map((obj, i) => {
                        return <td key={i}>{row[obj.name]}</td>;
                      })}

                  <td onClick={() => this.props.edit(row)}>
                    <a href="#"></a>
                  </td>
                </tr>
              );
            })}
          </Table.Body>
        </Table>
      </>
    );
  }
}
