import React from 'react';
import {
  Menu,
  Icon,
  Image,
  Dropdown,
  Container,
  Sticky,
} from 'semantic-ui-react';
import { Link } from 'react-router-dom';

// import { useAuth } from './contexts/AuthContext';
import { useAuth } from './contexts/AuthContext';

import { useHistory } from 'react-router-dom';

export default function Header() {
  const [activeItem, setActiveItem] = React.useState('');

  const { currentUser, logout } = useAuth();

  const history = useHistory();
  function handleClick(e, { name }) {
    setActiveItem(name);
  }

  async function handleLogout() {
    // setError("")

    try {
      await logout();
      history.push('/login');
    } catch {
      // setError("Failed to log out")
    }
  }

  return (
    <Menu secondary pointing>
      {/* <Menu.Item as={Link} to="/tictactoe">
        TicTacToe
      </Menu.Item> */}

     

      {/* <Menu.Item
        as={Link}
        to="/accounts"
        name="accounts"
        onClick={handleClick}
        active={activeItem === 'accounts'}
      >
        
      </Menu.Item> */}
      {/* <Menu.Item
        as={Link}
        to="/cates"
        name="cates"
        onClick={handleClick}
        active={activeItem === 'cates'}
      >
        
      </Menu.Item> */}

     

      {currentUser?.email == 'mkdodos@gmail.com' && (
        <Menu.Item
          as={Link}
          to="/stocks"
          name="stocks"
          onClick={handleClick}
          active={activeItem === 'stocks'}
        >
          
        </Menu.Item>
      )}

      <Menu.Item
        as={Link}
        to="/dashboard"
        name="dashboard"
        onClick={handleClick}
        active={activeItem === 'dashboard'}
      >
        
      </Menu.Item>

      <Menu.Item
        as={Link}
        to="/scores"
        name="scores"
        onClick={handleClick}
        active={activeItem === 'scores'}
      >
        
      </Menu.Item>

      <Menu.Item
        as={Link}
        to="/balances"
        name="balances"
        onClick={handleClick}
        active={activeItem === 'balances'}
      >
        
      </Menu.Item>

      <Dropdown item icon="setting">
        <Dropdown.Menu>
          {/* <Dropdown.Item>
            <Icon name='dropdown' />
            <span className='text'>New</span>
            <Dropdown.Menu>
              <Dropdown.Item>Document</Dropdown.Item>
              <Dropdown.Item>Image</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown.Item> */}
         
         <Dropdown.Item as={Link} to="/accounts" name="accounts">
            
          </Dropdown.Item>
          <Dropdown.Item as={Link} to="/cates">
            
          </Dropdown.Item>
         
          {/* <Dropdown.Item>Edit Permissions</Dropdown.Item>
          <Dropdown.Divider />
          <Dropdown.Header>Export</Dropdown.Header>
          <Dropdown.Item>Share</Dropdown.Item> */}
        </Dropdown.Menu>
      </Dropdown>



      <Menu.Menu position="right">
        {currentUser ? (
          <Menu.Item name="" onClick={handleLogout}>
            <Icon name="sign-out" />
          </Menu.Item>
        ) : (
          <Menu.Item name="login" as={Link} to="/login">
            Login
          </Menu.Item>
        )}
      </Menu.Menu>
    </Menu>
  );
}
